.admin-profile {
  margin: 20px;
  padding: 20px;
  border-radius: 8px;
  background-color: #f5f5f5;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.admin-profile h2 {
  margin-bottom: 20px;
  color: #333;
}

.admin-profile p {
  margin: 10px 0;
  font-size: 16px;
  color: #555;
}

.admin-profile p strong {
  color: #000;
}
