/* General Styles */
.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #0066cc, #050066e9);
  padding: 20px;
}

.form {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  max-width: 400px;
  width: 100%;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

h1 {
  font-size: 28px;
  color: #003366;
  text-align: center;
  margin-bottom: 20px;
  font-family: "Poppins", sans-serif;
  letter-spacing: 1px;
}

p {
  font-size: 18px;
  text-align: center;
  background-color: #b0d4f1;
  padding: 8px;
  border-radius: 5px;
  margin-bottom: 15px;
  font-weight: 500;
}

.label {
  font-weight: 600;
  color: #003366;
  margin-bottom: 5px;
  font-family: "Roboto", sans-serif;
}

.input {
  border-radius: 8px;
  padding: 10px;
  border: 1px solid #ced4da;
  font-size: 14px;
  width: 100%;
  margin-bottom: 15px;
  transition: border-color 0.3s ease;
}

.input:focus {
  border-color: #003366;
  outline: none;
  box-shadow: 0 0 4px rgba(0, 51, 102, 0.2);
}

.btn {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 15px;
  color: #ffffff;
  background: linear-gradient(135deg, #005488, #0033667e);
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
}

.btn:hover {
  background: linear-gradient(135deg, #004080, #00224427);
  transform: scale(1.05);
}

.form-container .ant-form-item {
  margin-bottom: 20px;
}

.form-container .ant-form-item-label > label {
  color: #003366;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
}

.login-link p {
  font-size: 16px;
  color: #003366;
  margin-top: 15px;
}

.login-link p:hover {
  color: #002244;
  text-decoration: underline;
  transition: color 0.3s ease;
}

/* Focus states and interactive elements */
.input:focus {
  outline: none;
  border-color: #003366;
  box-shadow: 0 0 4px rgba(0, 51, 102, 0.3);
}
