/* EquipmentList.css */

.equipment-page {
  padding: 20px;
}

.equipment-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.equipment-item {
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
}

.equipment-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.equipment-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.equipment-details {
  padding: 15px;
  text-align: center;
}

.equipment-details h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.equipment-details p {
  font-size: 1rem;
  margin-bottom: 10px;
  color: #555;
}

button {
  background-color: #007bff;
  border: none;
  color: white;
  padding: 10px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 0.9rem;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

.delete-button {
  background-color: #dc3545;
}

.delete-button:hover {
  background-color: #c82333;
}

.close-button {
  background-color: #6c757d;
}

.close-button:hover {
  background-color: #5a6268;
}

.error {
  color: #dc3545;
  font-size: 1rem;
}
