/* Home.css */

/* General Styles */
.homepage {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  padding: 0 20px;
  background-color: #fafafa; /* Light background for contrast with dark sidebar */
}

/* Hero Section */
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
  position: relative;
  background: linear-gradient(
    135deg,
    #e0f7fa,
    #ffffff
  ); /* Subtle gradient for a premium look */
  border-radius: 15px;
  padding: 50px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  margin-top: 4npm 0px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hero:hover {
  transform: scale(1.02); /* Subtle hover effect for interactivity */
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
}

.hero-content {
  max-width: 50%;
}

.hero-heading {
  font-size: 3.8rem;
  color: #003366;
  font-weight: 800;
  margin-bottom: 25px;
  letter-spacing: 1.5px; /* Modern typography */
}

.hero-description {
  font-size: 1.3rem;
  color: #666;
  line-height: 1.9;
  margin-bottom: 35px;
}

.hero-buttons {
  display: flex;
  gap: 25px;
}

.hero-image {
  max-width: 45%;
}

.banner-image {
  width: 100%;
  border-radius: 15px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Services Section */
.services {
  text-align: center;
  margin-bottom: 80px;
}

.section-heading {
  font-size: 2.8rem;
  color: #003366;
  font-weight: 700;
  margin-bottom: 50px;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.section-heading::after {
  content: "";
  width: 50px;
  height: 3px;
  background-color: #388e3c;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}

.services-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 30px;
}

.service-item {
  background-color: #fff;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
  max-width: 320px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.25);
}

.service-icon {
  font-size: 2.5rem;
  color: #003366;
  margin-bottom: 25px;
}

.service-item h3 {
  font-size: 1.9rem;
  color: #003366;
  font-weight: 700;
  margin-bottom: 20px;
}

.service-item p {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.7;
}

/* Features Section */
.features {
  background-color: #f9fafc; /* Soft neutral background */
  padding: 70px 20px;
  text-align: center;
  margin-bottom: 80px;
}

.features-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 30px;
}

.feature-item {
  background-color: #fff;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
  max-width: 320px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.25);
}

.feature-icon {
  font-size: 2.5rem;
  color: #003366;
  margin-bottom: 25px;
}

.feature-item h3 {
  font-size: 1.9rem;
  color: #003366;
  font-weight: 700;
  margin-bottom: 20px;
}

.feature-item p {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.7;
}

/* Testimonials Section */
.testimonials {
  background-color: #fff;
  padding: 70px 20px;
  text-align: center;
  margin-bottom: 80px;
}

.testimonials-container {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.testimonial-item {
  background-color: #f4f6f9;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
  max-width: 420px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
}

.testimonial-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.25);
}

.testimonial-item p {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.7;
  margin-bottom: 20px;
}

.testimonial-item h4 {
  font-size: 1.3rem;
  color: #003366;
  font-weight: 700;
}

.testimonial-item span {
  font-size: 1.1rem;
  color: #777;
}

/* Call-to-Action Section */
.cta {
  background-color: #003366;
  color: #fff;
  padding: 70px 20px;
  text-align: center;
  border-radius: 20px;
}

.cta-heading {
  font-size: 2.8rem;
  margin-bottom: 25px;
}

.cta-description {
  font-size: 1.3rem;
  margin-bottom: 40px;
}

.cta-button {
  background-color: #388e3c;
  color: #fff;
  border: none;
  padding: 18px 35px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.cta-button:hover {
  background-color: #2c6b2f;
  transform: scale(1.07); /* Slightly larger hover effect */
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
  }

  .hero-content {
    max-width: 100%;
    text-align: center;
  }

  .hero-image {
    max-width: 100%;
    margin-top: 30px;
  }

  .services-container,
  .features-container,
  .testimonials-container {
    flex-direction: column;
  }

  .service-item,
  .feature-item,
  .testimonial-item {
    max-width: 100%;
  }
}
