/* ShowDoctors.css */
.text-center {
  text-align: center;
  margin-bottom: 24px;
  color: #333;
}

.doctors-list-title {
  font-size: 2rem; /* Larger title for prominence */
  font-weight: 700; /* Bold title */
  color: #1890ff; /* Primary color for title */
}

.doctor-card {
  border: 1px solid #e0e0e0; /* Subtle border */
  border-radius: 12px; /* Rounded corners */
  padding: 16px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1); /* Soft shadow */
  background-color: #ffffff; /* White background */
  transition: box-shadow 0.3s, transform 0.3s; /* Smooth transition */
  height: 100%;
}

.doctor-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Stronger shadow on hover */
  transform: translateY(-4px); /* Slight lift effect */
}

.doctor-card h1 {
  font-size: 1.5rem; /* Title font size */
  font-weight: 600; /* Bold title */
  margin-bottom: 16px; /* Space below title */
  color: #1890ff; /* Primary color for title */
}

.doctor-card p {
  margin: 8px 0; /* Space between information items */
  font-size: 0.875rem; /* Font size for details */
  color: #595959; /* Dark gray text color */
}

.doctor-card p strong {
  color: #333; /* Dark text color for labels */
}
