/* Profile.css */

/* Container for the entire profile page */
.profile-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: #b91b1b35;
  margin-top: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Header styling */
.profile-header {
  text-align: center;
  margin-bottom: 20px;
}

.profile-header h1 {
  font-size: 2rem;
  color: #333;
}

/* Profile information section */
.profile-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.profile-info p {
  font-size: 1rem;
  color: #555;
}

.profile-info strong {
  color: #333;
}

/* Button styling */
.profile-button {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
}

.profile-button:hover {
  background-color: #0056b3;
}

/* Responsive design */
@media (max-width: 768px) {
  .profile-info {
    text-align: center;
  }

  .profile-button {
    display: block;
    width: 100%;
  }
}
