/* ShowNurses.css */
.card {
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: all 0.3s ease;
  background-color: #fff;
  border: 1px solid #eaeaea;
}

.card:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  transform: scale(1.02);
}

.nurse-image {
  width: 100%;
  height: auto;
  border-bottom: 1px solid #eaeaea;
  object-fit: cover;
}

.card h1 {
  font-size: 1.5rem;
  margin: 16px 0;
  color: #333;
}

.card p {
  font-size: 1rem;
  color: #555;
  margin: 8px 0;
}

.card p strong {
  color: #000;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  color: #fff;
  background-color: #007bff;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
}

.btn:hover {
  background-color: #0056b3;
}

.btn-primary {
  background-color: #28a745;
}

.btn-primary:hover {
  background-color: #218838;
}

.text-center {
  text-align: center;
}
