.video-page {
  padding: 20px;
  background-color: #f9f9f9;
}

.video-page h2 {
  text-align: center;
  color: #003366;
  margin-bottom: 30px;
}

.video-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  justify-items: center;
}

.video-card {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.video-card h4 {
  margin-top: 15px;
  color: #333;
}

.video-card iframe {
  width: 100%;
  border-radius: 8px;
}
