.apply-doctor-container {
  padding: 30px;
  background-color: #f2f1eef0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.apply-doctor-form {
  max-width: 900px;
  margin: 0 auto;
}

.text-center {
  text-align: center;
  margin-bottom: 20px;
}

.form-divider {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 20px;
}

.form-input {
  border-radius: 8px;
  border: 2px solid #d9d9d9;
  padding: 10px;
  font-weight: 500;
  transition: border-color 0.3s;
}

.form-input:hover,
.form-input:focus {
  border-color: #40a9ff;
}

.ant-form-item-label > label {
  color: #333;

  font-weight: 700;
}

.ant-form-item {
  margin-bottom: 20px;
}

.submit-btn {
  background-color: #1890ff;
  border-color: #1890ff;
  border-radius: 8px;
  padding: 10px;
}

.submit-btn:hover {
  background-color: #40a9ff;
  border-color: #40a9ff;
}
