/* Doctors.css */

.doctors-list-title {
  font-size: 2rem;
  font-weight: bold;
  color: #003366;
  margin-bottom: 20px;
  letter-spacing: 1px;
}

.doctor-card {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-radius: 12px;
  overflow: hidden;
  background: #f5f5f5;
  min-height: 150px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.doctor-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.doctor-card .ant-card-head {
  background-color: #1890ff;
  color: #fff;
  font-size: 1.1rem;
  padding: 10px 16px;
  border-bottom: 1px solid #e8e8e8;
}

.doctor-card .ant-card-body {
  padding: 16px;
}

.doctor-card p {
  margin-bottom: 8px;
  color: #333;
  font-size: 0.9rem;
}
