/* About.css */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 20px;
  background-color: #f4f4f4;
}

.content {
  text-align: center;
  margin-bottom: 40px;
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.heading {
  font-size: 3rem;
  color: #003366;
  margin-bottom: 20px;
  font-weight: bold;
}

.paragraph {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.8;
  margin-bottom: 20px;
}

.mapContainer {
  width: 100%;
  height: 400px;
  max-width: 800px;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}

.map {
  width: 100%;
  height: 100%;
}

.contact-info {
  font-size: 1.1rem;
  color: #333;
  margin-top: 20px;
}

.contact-info h2 {
  font-size: 1.5rem;
  color: #003366;
  margin-bottom: 10px;
}

.contact-info p {
  margin-bottom: 8px;
}

.contact-info a {
  color: #0066cc;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}
