.owner-info-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f7f9fc;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.owner-info-heading {
  text-align: center;
  font-size: 36px;
  color: #003366;
  margin-bottom: 20px;
}

.owner-details {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}

.owner-photo {
  flex: 1;
  text-align: center;
}

.owner-image {
  width: 100%;
  max-width: 300px;
  border-radius: 50%;
  border: 5px solid #003366;
}

.owner-description {
  flex: 2;
  font-size: 18px;
  line-height: 1.6;
  color: #333;
}

.owner-description h2 {
  font-size: 28px;
  color: #003366;
  margin-bottom: 10px;
}

.location-section {
  margin-top: 40px;
}

.location-section h2 {
  font-size: 28px;
  color: #003366;
  text-align: center;
  margin-bottom: 20px;
}

.map-container {
  width: 100%;
  height: 450px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
